import { Button, Form, ListGroup } from 'react-bootstrap'
import React from "react";

export default class Chat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            list: [
                { id: "GG", text: "smida" }
            ]
        };
        this.id = ""
        this.text = ""
        this.addChild = this.addChild.bind(this);
        this.handleIdChange = this.handleIdChange.bind(this);
        this.handleTextChange = this.handleTextChange.bind(this);
    }

    addChild(event) {
        // State change will cause component re-render

        var a = this.state
        a.list.push({ id: this.id, text: this.text })
        this.setState(a)
    }

    handleIdChange(event) {
        this.id = event.target.value
    }

    handleTextChange(event) {
        this.text = event.target.value
    }

    render() {
        return (
            <div style={{
                    border: "20px solid transparent"
                }}
            >   
                <Form.Group controlId="formBasicID">
                    <Form.Label>Name</Form.Label>
                    <Form.Control placeholder="Enter any ID, just don't make me caught." onChange={this.handleIdChange}/>
                </Form.Group>

                <Form.Group controlId="formBasicText">
                    <Form.Label>Text</Form.Label>
                    <Form.Control as="textarea" placeholder="Any Text" onChange={this.handleTextChange} rows={3}/>
                </Form.Group>
                <Button variant="primary" type="submit" onClick={this.addChild} >
                    Submit
                </Button>
                <ListGroup variant="flush">
                    {
                        this.state.list.map((item) => (
                            <ListGroup.Item>{item.id} : {item.text}</ListGroup.Item>
                        ))
                    }
                </ListGroup>
            </div>
        );
    }
}
